import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { Observable } from 'rxjs';
import { Offers } from 'src/app/models/OfferListDto'
import { Request, IItemsType } from 'src/app/models/Request';

@Injectable({
  providedIn: 'root'
})
export class OffersService {

  constructor(
    private http : HttpClient
  ) { }

   /**
     * @param filter (optional) 
     * @param userId (optional) 
     * @param sorting (optional) 
     * @param skipCount (optional) 
     * @param maxResultCount (optional) 
     * @return Success
     */
    getAllOffer(filter: string | null | undefined, MinPrice: number | null | undefined, MaxPrice: number | null | undefined, ServiceType: number | null | undefined): Observable<Request<IItemsType<Offers[]>>> {
      let url_ =  BaseUrl.baseUrl + "/api/services/app/Request/GetOffers?";
      if (filter !== undefined)
          url_ += "Filter=" + encodeURIComponent("" + filter) + "&"; 
      if (MinPrice !== undefined)
          url_ += "MinPrice=" + encodeURIComponent("" + MinPrice) + "&"; 
      if (MaxPrice !== undefined)
          url_ += "MaxPrice=" + encodeURIComponent("" + MaxPrice) + "&"; 
      if (ServiceType !== undefined)
          url_ += "ServiceType=" + encodeURIComponent("" + ServiceType) + "&"; 
      url_ = url_.replace(/[?&]$/, "");

      return this.http.get<any>(url_);
  }

    }